import axios from 'axios';
import Config from '../Config';

export default class ChainService {
  constructor(network) {
  }

  getTableUrl() {
    const url = `${Config.network.protocol}://
                 ${Config.network.host}: 
                 ${Config.network.port}/v1/chain/get_table_rows`;
    return url;    
  }

  async readTable(code, table) {
    return await axios.post(this.getTableUrl(), {
        code: code,
        table: table
      }
    );
  }
}